

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.users-list{
    text-align: center;
    padding: 60px;
}

.users-list li{
    border-top: 2px solid royalblue;
    padding: 60px;
    list-style: none;
}
.add-button{
    margin: 30px 0 0 60px;
    font-weight: bold;
    text-transform: uppercase;
    border: none;
    background-color: royalblue;
    color: white;
    font-size: 12px;
    letter-spacing: 2px;
    padding: 15px 10px;
    transition: .2s;
}

.add-button:hover{
    cursor: pointer;
    color: royalblue;
    box-shadow: 0 0 0 2px royalblue;
    background-color: transparent;
}
